.more {
    position: absolute;
    top: 14px;
    right: 10px;
    font-size: 2.2rem;
    font-weight: 600;
    color: #F1F1F1;

    &:hover {
        cursor: pointer;
        color: #E2DCC8;

    }
}

@media (max-width: 739px) {
    .more {
        top: 16px;
        font-size: 1.4rem;
    }
}